import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/products';

export const apiProductGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiProductPost = async (serviceUrl, product) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: product ? JSON.stringify(product) : null,
  };
  return request(url, options);
};

export const apiProductPut = async (serviceUrl, product) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: product ? JSON.stringify(product) : null,
  };
  return request(url, options);
};

export const apiProductDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
