import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  name: PropTypes.string.isRequired,
  description: PropTypes.string,
  interestRate: PropTypes.number.isRequired,
  productSegment: PropTypes.string.isRequired,
  image: PropTypes.string,
});

export const formValues = PropTypes.shape({
  name: PropTypes.string,
  description: PropTypes.string,
  interestRate: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  productSegment: PropTypes.string,
  image: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  description: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  interestRate: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  productSegment: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  image: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  name: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  description: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  interestRate: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  productSegment: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  image: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
