import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { formValues, formTouched, formErrors } from 'components/__types__/product';
import { withFormik } from 'formik';
import { withTranslation } from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { compose } from 'recompose';
import * as Yup from 'yup';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import ConfirmationDialogTrigger from 'components/common/ConfirmationDialogTrigger';

const styles = (theme) => ({
  root: {
    margin: theme.spacing(3),
  },
  button: {
    marginBottom: '10px',
  },
  downloadAnchor: {
    color: 'inherit',
    textDecoration: 'inherit',
    fontWeight: 'inherit',
    '&:link, &:visited, &:hover, &:active': {
      color: 'inherit',
      textDecoration: 'inherit',
      fontWeight: 'inherit',
    },
  },
  textField: {
    width: '100%',
  },
});
class ProductForm extends PureComponent {
  constructor(props) {
    super(props);
    this.handleConfirmationDialogAction = this.handleConfirmationDialogAction.bind(this);
  }

  handleConfirmationDialogAction(action) {
    const { onDelete, values } = this.props;
    switch (action) {
      case ConfirmationDialogTrigger.CONFIRM: {
        onDelete(values);
        break;
      }
      default:
        break;
    }
  }

  render() {
    const {
      classes,
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit: formikHandleSubmit,
      onDelete,
      onCancelEditing,
      isSubmitting,
      setFieldValue,
      t,
    } = this.props;

    const getHelperText = (field) => (errors[field] && touched[field] ? errors[field] : '');
    const getFormattedTime = () => {
      const today = new Date();
      return `${today.getFullYear()}${today.getMonth() + 1}${today.getDate()}`;
    };

    const handleFiles = (field) => (event) => {
      const uploadedFile = event.target;
      const reader = new FileReader();
      reader.onload = () => {
        const dataURL = reader.result;
        const imageData = dataURL.match(/data:([^;]*);base64,(.*)$/);
        if (imageData && imageData[1] && imageData[2]) {
          setFieldValue(field, imageData[2]);
          setFieldValue(`${field}ContentType`, imageData[1]);
        }
      };
      reader.readAsDataURL(uploadedFile.files[0]);
    };

    const handleSubmit = (e) => {
      e.stopPropagation(); // avoids double submission caused by react-shadow-dom-retarget-events
      formikHandleSubmit(e);
    };

    return (
      <form onSubmit={handleSubmit} className={classes.root} data-testid="product-form">
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              id="product-name"
              error={errors.name && touched.name}
              helperText={getHelperText('name')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.name}
              name="name"
              label={t('entities.product.name')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="product-description"
              error={errors.description && touched.description}
              helperText={getHelperText('description')}
              className={classes.textField}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.description}
              name="description"
              label={t('entities.product.description')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              id="product-interestRate"
              error={errors.interestRate && touched.interestRate}
              helperText={getHelperText('interestRate')}
              className={classes.textField}
              type="number"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.interestRate}
              name="interestRate"
              label={t('entities.product.interestRate')}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <InputLabel htmlFor="product-productSegment">
              {t('entities.product.productSegment')}
            </InputLabel>
            <Select
              native
              id="product-productSegment"
              error={errors.productSegment && touched.productSegment}
              className={classes.textField}
              value={values.productSegment}
              name="productSegment"
              onChange={handleChange}
            >
              {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
              <option value="" />
              <option value="PPC">PPC</option>
              <option value="SME_S">SME_S</option>
              <option value="SME_L">SME_L</option>
              <option value="XL">XL</option>
              <option value="XXL">XXL</option>
            </Select>
          </Grid>
          <Grid item xs={12} sm={6}>
            <InputLabel htmlFor="image-upload-file-button">
              {t('entities.product.image')}
            </InputLabel>
            <div>
              <input
                data-testid="image-uploader"
                style={{ display: 'none' }}
                id="image-upload-file-button"
                accept="image/*"
                type="file"
                onChange={handleFiles('image')}
              />
              <label htmlFor="image-upload-file-button">
                <Button className={classes.button} component="span">
                  {t('common.selectImageFile')}
                </Button>
              </label>
            </div>
            {values.image && (
              <div>
                <img src={`data:${values.imageContentType};base64, ${values.image}`} alt="" />
              </div>
            )}
          </Grid>
          {onDelete && (
            <ConfirmationDialogTrigger
              onCloseDialog={this.handleConfirmationDialogAction}
              dialog={{
                title: t('entities.product.deleteDialog.title'),
                description: t('entities.product.deleteDialog.description'),
                confirmLabel: t('common.yes'),
                discardLabel: t('common.no'),
              }}
              Renderer={({ onClick }) => (
                <Button onClick={onClick} disabled={isSubmitting}>
                  {t('common.delete')}
                </Button>
              )}
            />
          )}

          <Button onClick={onCancelEditing} disabled={isSubmitting} data-testid="cancel-btn">
            {t('common.cancel')}
          </Button>

          <Button type="submit" color="primary" disabled={isSubmitting} data-testid="submit-btn">
            {t('common.save')}
          </Button>
        </Grid>
      </form>
    );
  }
}

ProductForm.propTypes = {
  classes: PropTypes.shape({
    root: PropTypes.string,
    textField: PropTypes.string,
    submitButton: PropTypes.string,
    button: PropTypes.string,
    downloadAnchor: PropTypes.string,
  }),
  values: formValues,
  touched: formTouched,
  errors: formErrors,
  handleChange: PropTypes.func.isRequired,
  handleBlur: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  onDelete: PropTypes.func,
  onCancelEditing: PropTypes.func,
  isSubmitting: PropTypes.bool.isRequired,
  setFieldValue: PropTypes.func.isRequired,
  t: PropTypes.func.isRequired,
  i18n: PropTypes.shape({ language: PropTypes.string }).isRequired,
};

ProductForm.defaultProps = {
  onCancelEditing: () => {},
  classes: {},
  values: {},
  touched: {},
  errors: {},
  onDelete: null,
};

const emptyProduct = {
  name: '',
  description: '',
  interestRate: '',
  productSegment: '',
  image: '',
};

const validationSchema = Yup.object().shape({
  name: Yup.string().required(),
  description: Yup.string(),
  interestRate: Yup.number().required().min(0),
  productSegment: Yup.string().required(),
  image: Yup.string(),
});

const formikBag = {
  mapPropsToValues: ({ product }) => product || emptyProduct,

  enableReinitialize: true,

  validationSchema,

  handleSubmit: (values, { setSubmitting, props: { onSubmit } }) => {
    onSubmit(values);
    setSubmitting(false);
  },

  displayName: 'ProductForm',
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withTranslation(),
  withFormik(formikBag)
)(ProductForm);
